.message{
    display: flex;
    align-items: center;
    padding: 20px;
}

.message > img{
    height: 50px;
    width: 50px;
    object-fit: contain;
    border-radius: 999px;
}

.message__info{
    padding-left: 10px;
}

.message__timestamp{
    color: gray;
    font-weight: 200;
    font-size: 13px;
}